<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Tambah Kelas</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-refresher slot="fixed" @ionRefresh="doRefresh($event)">
        <ion-refresher-content />
      </ion-refresher>
      <ion-content class="ion-padding">
        <ion-item>
          <ion-label position="floating">Nama Kelas</ion-label>
          <ion-input
            v-model="kelasName"
            placeholder="Masukkan nama kelas"
          ></ion-input>
        </ion-item>
        <ion-button expand="block" @click="addKelas" style="color: white"
          >Tambah Kelas</ion-button
        >

        <ion-grid class="ion-padding">
          <ion-row>
            <ion-col>
              <h2>Daftar Kelas</h2>
              <table>
                <thead>
                  <tr>
                    <th>Nama Kelas</th>
                    <th>Aksi</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="kelas in kelasList" :key="kelas.id">
                    <td>{{ kelas.name }}</td>
                    <td>
                      <ion-button color="warning" @click="openEditModal(kelas)"
                        >Edit</ion-button
                      >
                      <ion-button color="danger" @click="deleteKelas(kelas.id)"
                        >Hapus</ion-button
                      >
                    </td>
                  </tr>
                </tbody>
              </table>
            </ion-col>
          </ion-row>
        </ion-grid>
      </ion-content>

      <ion-modal :is-open="isEditModalOpen" @did-dismiss="closeEditModal">
        <ion-header>
          <ion-toolbar>
            <ion-title>Edit Kelas</ion-title>
            <ion-buttons slot="end">
              <ion-button @click="closeEditModal">Close</ion-button>
            </ion-buttons>
          </ion-toolbar>
        </ion-header>
        <ion-content>
          <ion-item>
            <ion-label position="floating">Nama Kelas</ion-label>
            <ion-input v-model="editKelasName"></ion-input>
          </ion-item>
          <ion-button expand="block" @click="updateKelas"
            >Simpan Perubahan</ion-button
          >
        </ion-content>
      </ion-modal>

      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>
    </ion-content>
  </ion-page>
</template>

<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonBackButton,
  IonContent,
  IonRefresher,
  IonRefresherContent,
  IonButton,
  IonGrid,
  IonRow,
  IonCol,
  IonModal,
  IonItem,
  IonLabel,
  IonInput,
  IonToast,
} from "@ionic/vue";
import { ref, onMounted } from "vue";
import {
  getFirestore,
  collection,
  addDoc,
  getDocs,
  updateDoc,
  deleteDoc,
  doc,
  serverTimestamp,
} from "firebase/firestore";
import { firebaseApp } from "../firebase.js";

const db = getFirestore(firebaseApp);
const kelasName = ref("");
const kelasList = ref([]);
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});
const isEditModalOpen = ref(false);
const editKelasName = ref("");
const editKelasId = ref("");

const fetchKelas = async () => {
  try {
    const querySnapshot = await getDocs(collection(db, "kelas"));
    kelasList.value = querySnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    }));
  } catch (error) {
    console.error("Error fetching kelas:", error);
    showToast("Terjadi kesalahan saat mengambil data kelas.", "danger");
  }
};

const addKelas = async () => {
  if (!kelasName.value) {
    showToast("Nama kelas tidak boleh kosong", "danger");
    return;
  }
  try {
    await addDoc(collection(db, "kelas"), {
      name: kelasName.value,
      createdAt: serverTimestamp(),
    });
    showToast("Kelas berhasil ditambahkan", "success");
    kelasName.value = ""; // Clear the input
    fetchKelas(); // Refresh the list
  } catch (error) {
    console.error("Error adding kelas:", error);
    showToast("Terjadi kesalahan saat menambahkan kelas", "danger");
  }
};

const openEditModal = (kelas) => {
  editKelasName.value = kelas.name;
  editKelasId.value = kelas.id;
  isEditModalOpen.value = true;
};

const closeEditModal = () => {
  isEditModalOpen.value = false;
  editKelasName.value = "";
  editKelasId.value = "";
};

const updateKelas = async () => {
  if (!editKelasName.value) {
    showToast("Nama kelas tidak boleh kosong", "danger");
    return;
  }
  try {
    await updateDoc(doc(db, "kelas", editKelasId.value), {
      name: editKelasName.value,
    });
    showToast("Kelas berhasil diperbarui", "success");
    closeEditModal();
    fetchKelas(); // Refresh the list
  } catch (error) {
    console.error("Error updating kelas:", error);
    showToast("Terjadi kesalahan saat memperbarui kelas", "danger");
  }
};

const deleteKelas = async (kelasId) => {
  try {
    await deleteDoc(doc(db, "kelas", kelasId));
    showToast("Kelas berhasil dihapus", "success");
    fetchKelas(); // Refresh the list
  } catch (error) {
    console.error("Error deleting kelas:", error);
    showToast("Terjadi kesalahan saat menghapus kelas", "danger");
  }
};

const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message,
    duration: 2000,
    color,
  };
};

const doRefresh = (event) => {
  setTimeout(() => {
    fetchKelas();
    event.target.complete();
  }, 2000);
};

onMounted(() => {
  fetchKelas();
});
</script>

<style scoped>
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th,
td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
  font-weight: bold;
}

tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

tbody tr:hover {
  background-color: #f1f1f1;
}
</style>
